body {
  font-family: "Quicksand", sans-serif;
}
 h2 {
    color: #00a2c6;
 }
  
 h3 {
    color: #960a6c;
 }
  
 footer {
    padding: 20px;
    color: #faf9f9;
    background-color: #8b146e;
 }
 nav a {
   	   font-size: 18px;
   	   font-weight: 400;
   	   text-decoration: none;
         color: white;
   	}

      nav a:hover {
   font-weight: bold;
	}
  
   	.featured-image {
      	    width: 75%;
      	    max-height: 300px;
            object-fit: cover;
            object-position: center;	}

.card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    padding: 20px;
    margin-top: 20px;
            	}
    .jumbotron {
        padding: 30px;
        background-color: #a52f91;
        text-align: center;
        font-size: 20px;
        color: white;
        }
            nav li {
            display: inline;
            list-style-type: none;
            margin-right: 20px;
            }
            nav {
                background-color: #00a2c6;
                padding: 5px;
                }
            body {
                font-family: 'Quicksand', sans-serif;
                margin: 0;
                padding: 0;
                }
            main {
                padding: 20px;
                overflow: auto;
                }
            *{
                box-sizing: border-box;
            }
            nav {
            background-color: #882178;
            padding: 5px;
            position: sticky;
            top: 0;
        }
    header {
     display: inline;
            }
 
	footer {
padding: 20px;
color: rgb(243, 246, 248);
background-color: #8b2676;
text-align: center;
font-weight: bold;
}
.profile header {
text-align: center;
}
main {
padding: 20px;
}
#content {
	float: left;
	width: 75%;
	  }
aside {
	  float: right;
	  width: 25%;
	  padding-left: 20px;
	}
main {
        padding: 20px;
        overflow: auto;
	}
* {
  box-sizing: border-box;
@media screen and (max-width: 1000px) {
#content,
aside {
    width: 100%;
   padding: 0;
  }
}
}
